import moment from "moment";
import service from "auth/FetchInterceptor";
import API_CONSTANTS from "constants/ApiConstant";
import Utils from "utils";
import { ACTION } from "constants/ManageOpenPoolContansts";
import { DATE_FORMAT_DD_MM_YYYY } from "constants/DateConstant";
const manageOpenPoolService = {};

manageOpenPoolService.preparedDataForExport = (data = []) => {
  const filtered = data.map(item => {
    return {
      employeeId: item.employeeId || " ",
      employeeName: item.employeeName || " ",
      functionalGroup: item.functionalGroup || " ",
      billableStatus: item.billableStatus || " ",
      previousExperience: item.previousExperience || " ",
      accountName: item.accountName || "",
      probableBillingDetails: item.probableBillingDetails || " ",
      billingStartDate: moment(item.billingStartDate).format(DATE_FORMAT_DD_MM_YYYY) || " ",
      ProjectName: item.ProjectName || " ",
      subDomain: item.subDomain || " ",
    };
  });
  return filtered;
};
manageOpenPoolService.preparedHeadersForPDF = (data = []) => {
  const indexes = data.map(x => x.dataIndex);
  const filtered = indexes.filter(x => x !== ACTION);
  return filtered;
};

manageOpenPoolService.getList = async function (params) {
  const { path, method } = API_CONSTANTS.MANAGE_OPEN_POOL_RESOURCE_LIST;
  try {
    const response = await service({
      url: path,
      method,
      params,
    });
    if (Utils.isEmpty(response)) {
      return [];
    }
    return response;
  } catch (e) {
    console.error("Error in getting the managePool list.", e);
  }
};
