manageOpenPoolService.prepareData = obj => {
  return obj.map(el => {
    return {
      id: el.id,
      employeeName: el.employeeName,
      employeeId: el.employeeId,
      functionalGroup: el.functionalGroup,
      accountName: el?.accountName,
      ProjectName: el?.ProjectName,
      billingStartDate: el.billingStartDate,
      probableBillingDetails: el.probableBillingDetails,
      billableStatus: el.billableStatus,
      projectId: el.projectId,
      billingEndDate: el.billingEndDate,
      Status: el.resourceStatus,
      accountId: el.accountId,
    };
  });
};
manageOpenPoolService.prepareRequestData = (billableStatus, probablebillingdetails, data) => {
  const datatoupdate = data[0];
  datatoupdate.billableStatus = billableStatus;
  datatoupdate.probableBillingDetails = probablebillingdetails;
  return datatoupdate;
};

export default manageOpenPoolService;
