import { useState } from "react";
import { Modal, Button, message, Form, Input, Select, DatePicker } from "antd";
import moment from "moment";
import { FORM_NAME, MODAL_ADD_BUTTON_TYPE, MODAL_BACK_BUTTON, VALIDATION_FAILED } from "constants/CommonConstants";
import { DATE_FORMAT_YYYY_MM_dd } from "constants/DateConstant";
import manageOpenPoolService from "services/manageOpenPoolService";
import { CANCEL_TEXT, UPDATE_TEXT } from "../../../../constants/ManageAccounts";
import {
  PROJECT_DATE_REQ,
  PROJECT_STATUS_REQ,
  UPDATE_SUCCESS,
  ADD_SUCCESS,
  SELECT_HIGHT,
  SELECT_LAYOUT,
  CONFIRM_MESSAGE,
} from "../../../../constants/ManageProjectsConstant";
import { COMMENT, EDIT_MANAGE_FORM_TITLE } from "constants/ManageOpenPoolContansts";
import { BILLABLE_STATUS, PROBABLE_BILLING_DATE } from "../../../../constants/ManageOpenPoolContansts";

const EditManageOpenPoolForm = ({ show, isEdit, onCancel, editAccounts }) => {
  const { confirm } = Modal;
  const { Option, OptGroup } = Select;
  const [form] = Form.useForm();
  const [probableBillingDate, setprobableBillingDate] = useState(null);
  const [bilableStatus, setBilableStatus] = useState(null);
  const [date, setdate] = useState(isEdit ? moment(editAccounts?.probableBillingDetails) : []);

  const billing_Status = ["Shadow", "Confirmed", "Billable", "Trainee", "Non-Billable", "Reserved"];
  const formItemLayout = {
    labelCol: { span: 8 },
    wrapperCol: { span: 14 },
  };
  const handleConfirm = () => {
    if (form.isFieldsTouched()) {
      confirm({
        title: CONFIRM_MESSAGE.value,
        centered: true,
        onOk() {
          onCancel();
        },
      });
    } else onCancel();
  };
  const success = () => {
    isEdit ? message.success(UPDATE_SUCCESS.value) : message.success(ADD_SUCCESS.value);
  };

  const handleOk = async e => {
    try {
      let formValues = await form.validateFields();
      setprobableBillingDate(moment(new Date(probableBillingDate)).unix());
      const probablebillingdetails = [
        { comment: formValues.comment, probableBillingDate: new Date(formValues.probableBillingDate._d) },
      ];
      const data = manageOpenPoolService.prepareData([editAccounts]);
      const datatoupdate = manageOpenPoolService.prepareRequestData(
        formValues.billableStatus,
        probablebillingdetails,
        data,
      );
      if (editAccounts && form.isFieldsTouched()) {
        const res = await manageOpenPoolService.UpdateResource(
          {
            loginEmpId: editAccounts?.employeIdrecord,
          },
          datatoupdate,
        );
        success();
      } else {
        success();
      }
      form.resetFields();
      onCancel();
    } catch (info) {
      console.log(VALIDATION_FAILED.value, info);
    }
  };
  let initials = {
    billableStatus: editAccounts?.billableStatus,
    probableBillingDate: editAccounts?.probableBillingDetails ? moment(editAccounts?.probableBillingDetails) : "",
  };
  const handleComment = value => {
    setBilableStatus(value);
  };
  return (
    <Modal
      visible={show}
      title={EDIT_MANAGE_FORM_TITLE.value}
      onCancel={handleConfirm}
      onOk={handleOk}
      bodyStyle={{ overflowY: SELECT_HIGHT.text, maxHeight: SELECT_HIGHT.value }}
      footer={[
        <Button key={MODAL_BACK_BUTTON.value} onClick={handleConfirm}>
          {CANCEL_TEXT.text}
        </Button>,

        <Button key={MODAL_ADD_BUTTON_TYPE.value} onClick={handleOk} type={MODAL_ADD_BUTTON_TYPE.value}>
          {UPDATE_TEXT.text}
        </Button>,
      ]}
    >
      <Form form={form} name={FORM_NAME.text} layout={SELECT_LAYOUT.text} {...formItemLayout} initialValues={initials}>
        <Form.Item
          name={BILLABLE_STATUS}
          label={BILLABLE_STATUS}
          rules={[
            {
              required: true,
              message: PROJECT_STATUS_REQ.text,
            },
          ]}
        >
          <Select placeholder={BILLABLE_STATUS} onChange={handleComment}>
            <OptGroup label={BILLABLE_STATUS}>
              {billing_Status.map(e => {
                return (
                  <Option key={e} value={e}>
                    {e.charAt(0).toUpperCase() + e.slice(1)}
                  </Option>
                );
              })}
            </OptGroup>
          </Select>
        </Form.Item>

        <Form.Item
          label={PROBABLE_BILLING_DATE}
          name={PROBABLE_BILLING_DATE}
          rules={[
            {
              required: true,
              message: PROJECT_DATE_REQ.text,
            },
          ]}
        >
          <DatePicker format={DATE_FORMAT_YYYY_MM_dd} />
        </Form.Item>
        <Form.Item
          name={COMMENT}
          label={COMMENT}
          rules={[
            {
              message: CONFIRM_MESSAGE.value,
            },
          ]}
        >
          <Input />
        </Form.Item>
      </Form>
    </Modal>
  );
};

export default EditManageOpenPoolForm;
