import { PageHeader, Radio, Button, Row, Col, Divider, Tooltip } from "antd";
import { SyncOutlined, SearchOutlined, EditOutlined } from "@ant-design/icons";
import moment from "moment";
import Checkbox from "antd/es/checkbox";
import EditManageOpenPoolForm from "./editManageOpenPoolForm";
import Flex from "components/shared-components/Flex";
import { Menu } from "antd";
import React, { useEffect, useState, useRef, useMemo } from "react";
import { isBoolean } from "lodash";
import FileHandler from "utils/fileHandler";
import manageOpenPoolService from "services/manageOpenPoolService";
import { ACCOUNT, SUB_DOMAINS } from "constants/AddSubDomainConstants";
import { PROJECT_COLUMN } from "constants/ManageProjects";
import {
  BILLABILITY_STATUS_LABEL,
  EMPLOYEE_ID_LABEL,
  EMPLOYEE_NAME_LABEL,
  EXPERIENCE_LABEL,
  FUNCTIONAL_GROUP_LABEL,
  SORT_DIRECTIONS,
  ACTIVE_STATUS,
  ALL_STATUS,
  INACTIVE_STATUS,
  LIGHT_NAVY_BLUE_COLOR,
  ACTION_TITLE,
} from "constants/CommonConstants";

import {
  PROBABLE_BILLING_DETAILS,
  BILLING_START_DATE,
  MENU_ITEMS,
  MENU_ITEMS_TYPES,
  MANAGE_POOL_TITLE,
  ACTION,
} from "constants/ManageOpenPoolContansts";
import { DATE_FORMAT_DD_MM_YYYY } from "constants/DateConstant";
import { ACTION_ACTIVE } from "../../../../constants/ManageOpenPoolContansts";
import TableComponent from "components/shared-components/TableComponent/Index";
import Utils from "utils";
import SearchFilterDropdown from "components/shared-components/SearchFilter";

const ManageOpenPool = () => {
  let searchInput = useRef(null);
  const [searchText, setsearchText] = useState("");
  const currentVisibleTableData = [];
  const [searchedColumn, setsearchedColumn] = useState("");
  const [loading, setLoading] = useState(false);
  const [benchData, setBenchData] = useState([]);
  const [value, setValue] = useState(ACTIVE_STATUS.value);
  const [filtered, setfiltered] = useState([]);
  const [filteredInfo, setFilteredInfo] = useState({});
  const [show, setShow] = useState(false);
  const [hide, setHide] = useState(false);
  let accountstatus = {};
  const [editAccounts, setEditAccounts] = useState(null);
  const [visibility, setVisibility] = useState({
    employeeId: true,
    employeeName: true,
    functionalGroup: true,
    history: true,
    billableStatus: true,
    previousExperience: true,
    accountName: true,
    probableBillingDetails: true,
    billingStartDate: true,
    ProjectName: true,
    subDomain: true,
    actions: true,
  });
  const [isMenuVisible, setIsMenuVisible] = useState(false);

  const todayDate = new Date().toISOString().slice(0, 10);

  const [sortedInfo, setSortedInfo] = useState({
    order: "ascend",
    columnKey: "",
  });
  useEffect(() => {
    getBenchData();
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);
  const getBenchData = async () => {
    try {
      setLoading(true);
      const respJson = await manageOpenPoolService.getList({ type: "All", onDate: todayDate, filter: "-1" });
      const bench = prepareBenchDataObj(respJson);
      setBenchData(bench);
      setfiltered(typeof bench !== bench?.filter(el => el.status?.toLowerCase() === ACTION_ACTIVE.toLowerCase()));
    } catch (e) {
      console.error("Error in getting open pool data", e);
    } finally {
      setLoading(false);
    }
  };
  const prepareBenchDataObj = obj => {
    return obj?.map(el => {
      return {
        id: el.resource?.id,
        employeeName: el.employee.employeeName,
        employeeId: el.employee.employeeId,
        functionalGroup: el.employee.functionalGroup,
        previousExperience: el.employee.previousExperience,
        accountName: el.account?.accountName,
        status: el.account?.status,
        ProjectName: typeof el.project?.projectName === undefined ? null : el.project?.projectName,
        billingStartDate: el.resource?.billingStartDate,
        probableBillingDetails: el.resource?.probableBillingDetails?.map(x => x.probableBillingDate),
        billableStatus: el.resource?.billableStatus,
        subDomain: el.resource?.subDomain,
        projectId: el.project?.projectId,
        billingEndDate: el.resource?.billingEndDate,
        resourceStatus: el.resource?.status,
        accountId: el.account?.accountId,
      };
    });
  };

  const onChangeStatus = e => {
    setValue(e.target.value);
    let status = e.target.value;
    if (status === ACTIVE_STATUS.value) {
      setfiltered(benchData.filter(el => el.status?.toLowerCase() === ACTION_ACTIVE.toLowerCase()));
    } else if (status === INACTIVE_STATUS.value) {
      setfiltered(benchData.filter(el => el.status?.toLowerCase() !== ACTION_ACTIVE.toLowerCase()));
    } else {
      setfiltered(benchData);
    }
  };
  const handleChange = (pagination, filters, sorter, extra) => {
    setFilteredInfo(filters);
    setSortedInfo(sorter);
  };
  const clearFilters = () => {
    setFilteredInfo({});
  };
  const clearAll = () => {
    setFilteredInfo({});
    setSortedInfo({});
  };
  const handleSearch = (selectedKeys, confirm, dataIndex) => {
    confirm();
    setsearchText(selectedKeys[0]);
    setsearchedColumn(dataIndex);
  };
  const handleReset = (clearFilters, confirm) => {
    clearFilters();
    setsearchText("");
    confirm();
  };
  const handleRefresh = () => {
    getBenchData();
    setValue(ACTIVE_STATUS.value);
    clearAll();
    setFilteredInfo({});
    setSortedInfo({});
    clearFilters();
    setsearchText(null);
  };
  const EditAccount = data => {
    setEditAccounts(data);
    setShow(true);
  };
  const getColumnSearchProps = dataIndex => ({
    filterDropdown: ({ setSelectedKeys, selectedKeys, confirm, clearFilters }) => (
      <SearchFilterDropdown
        setSelectedKeys={setSelectedKeys}
        selectedKeys={selectedKeys}
        confirm={confirm}
        clearFilters={clearFilters}
        handleSearch={handleSearch}
        dataIndex={dataIndex}
        searchInput={searchInput}
        handleReset={handleReset}
      />
    ),
    filterIcon: filtered => <SearchOutlined color={filtered ? LIGHT_NAVY_BLUE_COLOR : undefined} />,
    onFilter: (value, record) => {
      if (dataIndex === BILLING_START_DATE.value) {
        const valuee = value.format(DATE_FORMAT_DD_MM_YYYY).toString();
        const timeStampToDate = moment(record[dataIndex]).format(DATE_FORMAT_DD_MM_YYYY).toString();

        return timeStampToDate.toString().toLowerCase().includes(valuee.toLowerCase());
      } else if (dataIndex === "probableBillingDetails") {
        const valuee = value.format(DATE_FORMAT_DD_MM_YYYY).toString();
        if (!record[dataIndex]) {
          return;
        } else {
          const timeStampToDate = moment(record[dataIndex][0]).format(DATE_FORMAT_DD_MM_YYYY).toString();
          return timeStampToDate.toString().toLowerCase().includes(valuee.toLowerCase());
        }
      }
      return record[dataIndex]?.toString().toLowerCase().includes(value.toLowerCase());
    },
    onFilterDropdownVisibleChange: visible => {
      if (visible) {
        setTimeout(() => searchInput && searchInput.select && searchInput.select());
      }
    },
    render: text => {
      if (dataIndex === "probableBillingDetails") {
        if (!text) {
          return text;
        } else {
          const timeStampToDate = moment(text[0]).format(DATE_FORMAT_DD_MM_YYYY).toString();
          return timeStampToDate;
        }
      } else if (dataIndex === BILLING_START_DATE.value) {
        const timeStampToDate = moment(text).format(DATE_FORMAT_DD_MM_YYYY).toString();
        return timeStampToDate;
      } else {
        return text;
      }
    },
  });
  const onColHide = data => {
    setVisibility({ ...visibility, [data.key]: !visibility[data.key] });
    const _visibility = { ...visibility, [data.key]: !visibility[data.key] };
  };
  const handleMenuVisibleChange = flag => {
    setIsMenuVisible(flag);
  };

  const manageOpenPoolList = [
    {
      title: (
        <Tooltip title={EMPLOYEE_ID_LABEL.text} placement="bottom">
          <span>{EMPLOYEE_ID_LABEL.text}</span>
        </Tooltip>
      ),
      dataIndex: EMPLOYEE_ID_LABEL.value,
      key: EMPLOYEE_ID_LABEL.value,
      isVisible: visibility.employeeId,
      width: 120,
      sorter: (a, b) => Utils.antdTableSorter(a, b, EMPLOYEE_ID_LABEL.value),
      sortDirections: SORT_DIRECTIONS,
      ...getColumnSearchProps(EMPLOYEE_ID_LABEL.value),
    },
    {
      title: (
        <Tooltip title={EMPLOYEE_NAME_LABEL.text} placement="bottom">
          <span>{EMPLOYEE_NAME_LABEL.text}</span>
        </Tooltip>
      ),
      dataIndex: EMPLOYEE_NAME_LABEL.value,
      key: EMPLOYEE_NAME_LABEL.value,
      width: 150,
      sorter: (a, b) => Utils.antdTableSorter(a, b, EMPLOYEE_NAME_LABEL.value),
      sortDirections: SORT_DIRECTIONS,
      isVisible: visibility.employeeName,
      ...getColumnSearchProps(EMPLOYEE_NAME_LABEL.value),
    },

    {
      title: (
        <Tooltip title={FUNCTIONAL_GROUP_LABEL.text} placement="bottom">
          <span>{FUNCTIONAL_GROUP_LABEL.text}</span>
        </Tooltip>
      ),
      dataIndex: FUNCTIONAL_GROUP_LABEL.value,
      key: FUNCTIONAL_GROUP_LABEL.value,
      width: 120,
      sorter: (a, b) => Utils.antdTableSorter(a, b, FUNCTIONAL_GROUP_LABEL.value),
      sortDirections: SORT_DIRECTIONS,
      isVisible: visibility.functionalGroup,
      ...getColumnSearchProps(FUNCTIONAL_GROUP_LABEL.value),
    },
    {
      title: (
        <Tooltip title={EXPERIENCE_LABEL.text} placement="bottom">
          <span>{EXPERIENCE_LABEL.text}</span>
        </Tooltip>
      ),
      dataIndex: EXPERIENCE_LABEL.preExperienceValue,
      key: EXPERIENCE_LABEL.preExperienceValue,
      width: 120,
      sorter: (a, b) => Utils.antdTableSorter(a, b, EXPERIENCE_LABEL.preExperienceValue),
      sortDirections: SORT_DIRECTIONS,
      isVisible: visibility.previousExperience,
      ...getColumnSearchProps(EXPERIENCE_LABEL.preExperienceValue),
    },
    {
      title: (
        <Tooltip title={ACCOUNT.text} placement="bottom">
          <span>{ACCOUNT.text}</span>
        </Tooltip>
      ),
      dataIndex: ACCOUNT.value,
      key: ACCOUNT.value,
      width: 120,
      sorter: (a, b) => Utils.antdTableSorter(a, b, ACCOUNT.value),
      sortDirections: SORT_DIRECTIONS,
      isVisible: visibility.accountName,
      ...getColumnSearchProps(ACCOUNT.value),
    },
    {
      title: (
        <Tooltip title={SUB_DOMAINS.text} placement="bottom">
          <span>{SUB_DOMAINS.text}</span>
        </Tooltip>
      ),
      dataIndex: SUB_DOMAINS.subDomainValue,
      key: SUB_DOMAINS.subDomainValue,
      width: 120,
      sorter: (a, b) => Utils.antdTableSorter(a, b, SUB_DOMAINS.subDomainValue),
      sortDirections: SORT_DIRECTIONS,
      isVisible: visibility.subDomain,
      ...getColumnSearchProps(SUB_DOMAINS.subDomainValue),
    },
    {
      title: (
        <Tooltip title={PROJECT_COLUMN.text} placement="bottom">
          <span>{PROJECT_COLUMN.text}</span>
        </Tooltip>
      ),
      dataIndex: PROJECT_COLUMN.valueOfProject,
      key: PROJECT_COLUMN.valueOfProject,
      width: 120,
      sorter: (a, b) => Utils.antdTableSorter(a, b, PROJECT_COLUMN.valueOfProject),
      sortDirections: SORT_DIRECTIONS,
      isVisible: visibility.ProjectName,
      ...getColumnSearchProps(PROJECT_COLUMN.valueOfProject),
    },

    {
      title: (
        <Tooltip title={BILLABILITY_STATUS_LABEL.text} placement="bottom">
          <span>{BILLABILITY_STATUS_LABEL.text}</span>
        </Tooltip>
      ),
      dataIndex: BILLABILITY_STATUS_LABEL.value,
      key: BILLABILITY_STATUS_LABEL.value,
      width: 120,
      sorter: (a, b) => Utils.antdTableSorter(a, b, BILLABILITY_STATUS_LABEL.value),
      sortDirections: SORT_DIRECTIONS,
      isVisible: visibility.billableStatus,
      ...getColumnSearchProps(BILLABILITY_STATUS_LABEL.value),
    },
    {
      title: (
        <Tooltip title={BILLING_START_DATE.text} placement="bottom">
          <span>{BILLING_START_DATE.text}</span>
        </Tooltip>
      ),
      dataIndex: BILLING_START_DATE.value,
      key: BILLING_START_DATE.value,
      width: 120,
      sorter: (a, b) => Utils.antdTableSorter(a, b, BILLING_START_DATE.value),
      sortDirections: SORT_DIRECTIONS,
      isVisible: visibility.billingStartDate,
      ...getColumnSearchProps(BILLING_START_DATE.value),
    },
    {
      title: (
        <Tooltip title={PROBABLE_BILLING_DETAILS.text} placement="bottom">
          <span>{PROBABLE_BILLING_DETAILS.text}</span>
        </Tooltip>
      ),
      dataIndex: PROBABLE_BILLING_DETAILS.value,
      key: PROBABLE_BILLING_DETAILS.value,
      width: 120,
      sorter: (a, b) => Utils.antdTableSorter(a, b, PROBABLE_BILLING_DETAILS.value),
      sortDirections: SORT_DIRECTIONS,
      isVisible: visibility.probableBillingDetails,
      ...getColumnSearchProps(PROBABLE_BILLING_DETAILS.value),
    },
    {
      title: (
        <Tooltip title={ACTION_TITLE.text} placement="bottom">
          <span>{ACTION_TITLE.text}</span>
        </Tooltip>
      ),
      dataIndex: ACTION,
      key: ACTION,
      width: 130,
      isVisible: isBoolean(visibility.actions) ? visibility.actions : true,
      render: (_, record) => {
        return (
          <Flex justifyContent="between">
            <Button
              onClick={() => {
                EditAccount(record);
              }}
            >
              <EditOutlined />
            </Button>
          </Flex>
        );
      },
    },
  ];
  const onMenuItem = ({ key }) => {
    switch (key) {
      case MENU_ITEMS_TYPES.clearAllFilters:
        clearFilters();
        break;
      case MENU_ITEMS_TYPES.exportAllExcel:
      case MENU_ITEMS_TYPES.exportVisibleExcel:
        exportToExcel(key);
        break;
      case MENU_ITEMS_TYPES.exportAllPDF:
      case MENU_ITEMS_TYPES.exportVisiblePDF:
        exportToPDF(key);
        break;
      default:
        return;
    }
  };
  const menu = (
    <Menu onClick={onMenuItem}>
      {MENU_ITEMS.map(menu => (
        <>
          <Menu.Item key={menu.type}>{menu.title}</Menu.Item>
          <Divider className="m-0" />
        </>
      ))}
      <Menu.ItemGroup key="table_column" title="Column">
        {manageOpenPoolList.map(item =>
          item.key !== ACTION ? (
            <>
              <Menu.Item key={item.key} onClick={() => onColHide(item)}>
                <Checkbox checked={item.isVisible}>{item.title}</Checkbox>
              </Menu.Item>
            </>
          ) : null,
        )}
      </Menu.ItemGroup>
    </Menu>
  );
  const exportToExcel = type => {
    const data =
      type === MENU_ITEMS_TYPES.exportAllExcel
        ? manageOpenPoolService.preparedDataForExport(filtered)
        : manageOpenPoolService.preparedDataForExport(currentVisibleTableData);
    FileHandler.exportToExcel(data, { fontSize: 15, padding: 5 }, "pool");
    setIsMenuVisible(false);
  };

  const exportToPDF = type => {
    const data =
      type === MENU_ITEMS_TYPES.exportAllPDF
        ? manageOpenPoolService.preparedDataForExport(filtered)
        : manageOpenPoolService.preparedDataForExport(currentVisibleTableData);
    const headers = manageOpenPoolService.preparedHeadersForPDF(manageOpenPoolList);

    FileHandler.exportToPDF(headers, data, { fontSize: 11, padding: 3 }, 400, 400, MANAGE_POOL_TITLE);
    setIsMenuVisible(false);
  };
  const filteredColumns = useMemo(() => manageOpenPoolList.filter(item => item.isVisible), [visibility]);

  return (
    <>
      <Row align="middle" gutter={[20, 20]} className="mb-2">
        <Col xs={24} md={12} lg={12} xl={8}>
          <PageHeader
            className="p-2 pl-2"
            title={MANAGE_POOL_TITLE}
            subTitle={
              <Button className="ml-4" onClick={handleRefresh}>
                <SyncOutlined shape="circle" />
              </Button>
            }
          />
        </Col>

        <Col xs={24} md={12} lg={12} xl={16} className="pr-0 text-left text-md-right text-lg-left text-lg-right">
          <Radio.Group onChange={onChangeStatus} value={value}>
            <Radio value={ACTIVE_STATUS.value}>{ACTIVE_STATUS.text}</Radio>
            <Radio value={INACTIVE_STATUS.value}>{INACTIVE_STATUS.text}</Radio>
            <Radio className="m-0" value={ALL_STATUS.value}>
              {ALL_STATUS.text}
            </Radio>
          </Radio.Group>
        </Col>
      </Row>
      <TableComponent
        rowKey={record => record.emailId}
        columns={filteredColumns}
        data={filtered}
        scroll={{ x: 2300 }}
        visibleData={data => {
          currentVisibleTableData.push(...data);
        }}
        handleChange={handleChange}
        tableSize="small"
        dropdownMenu={menu}
        visible={isMenuVisible}
        onDropdownMenuChange={handleMenuVisibleChange}
        loading={loading}
      />
      {show && (
        <EditManageOpenPoolForm
          show={show}
          onCancel={() => setShow(false)}
          setShow={setShow}
          editAccounts={editAccounts}
        />
      )}
    </>
  );
};
export default ManageOpenPool;
